import counter from '../counter/counter.vue';
export default {
	data() {
		return {};
	},
	props: {
		spaceInfo: {
			type: Object,
			default: () => {}
		},
		goodsInfo: {
			type: Object,
			default: () => {}
		},
		goodsData: {
			type: Object,
			default: () => {}
		}
	},
	components: {
		counter
	},
	methods: {
		hideSpecifications() {
			this.$emit('hideSpec');
		},
		handleSelectSpecification(item) {
			// 子级注册事件 父级执行事件
			this.$emit('setSelectSpec', item);
		},
		sub() {
			this.$emit('sub');
		},
		add() {
			this.$emit('add');
		}
	}
};
